
.advantages__wrapper {
  margin: 0 auto;
  max-width: 1920px;
  padding: 120px clamp($wrapper-padding-desktop-min, $wrapper-padding-desktop, $wrapper-padding-desktop-max);

  @include vp-1439 {
    padding-left: 7%;
    padding-right: 7%;
  }

  @include vp-1023 {
    padding: 80px clamp($wrapper-padding-tablet-min, $wrapper-padding-tablet, $wrapper-padding-tablet-max) 40px;
  }

  @include vp-767 {
    padding: 50px clamp($wrapper-padding-mobile-min, $wrapper-padding-mobile, $wrapper-padding-mobile-max) 25px;
  }
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 30px;
  column-gap: 31px;

  @include vp-1023 {
    margin-left: 0;
    gap: 30px;
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.advantages__item {
  display: flex;
  position: relative;
  flex-direction: column;
  padding: 40px 40px 35px 45px;
  box-sizing: border-box;
  border: 1px solid $color-whisper;
  border-left: none;
  min-height: 300px;

  &::before {
    content: "";
    display: block;
    width: 5px;
    height: calc(2px + 100%);
    position: absolute;
    top: -1px;
    left: 0;
    background-color: $color-deep-sky-blue;
  }

  @include hover {
    color: $color-deep-sky-blue;
    background-color: $color-deep-sky-blue;
    border: 1px solid $color-deep-sky-blue;

    .advantages__logo,
    h3,
    p {
      color: $color-alice-blue;
    }
  }

  @include vp-1023 {
    padding: 40px 30px 30px 45px;
    min-height: 360px;
  }

  @include vp-767 {
    padding: 30px 30px 35px 30px;
    min-height: 340px;
  }

  &--small {
    @include vp-767 {
      min-height: 325px;

      .advantages__logo {
        margin-bottom: 18px;
      }

      p {
        margin-top: 5px;
      }
    }
  }

  &--wrong-height {
    @include vp-1023 {
      // "не винвоатая я - макет кривой ("
      p {
        margin-top: -8px;
      }
    }
  }
}

.advantages__logo {
  margin-bottom: 20px;
  display: inline-flex;
  vertical-align: baseline;
  color: $color-deep-sky-blue;

  @include vp-1023 {
    margin-bottom: 15px;
  }

  @include vp-767 {
    margin-bottom: 15px;
  }

  & svg {
    @include vp-767 {
      width: 30px;
      height: 30px;
    }
  }
}

.advantages h3 {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;
  font-family: "Roboto Condensed", $font-sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 35px;
  color: $color-deep-sky-blue;

  @include vp-1023 {
    line-height: 138%;
    margin-bottom: 10px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 140%;
  }
}

.advantages p {
  margin: 0;
  padding: 0;
  line-height: 135%;
  color: $color-black-pearl;

  @include vp-1023 {
    font-size: 16px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 135%;
  }
}
