.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  min-height: 100%;
  padding-top: 146px;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;
  border-radius: 3px;
  margin-right: 16px;

  width: 500px;
  min-width: 400px;
  padding: 37px 40px 45px 35px;

  background-color: $color-deep-sky-blue;

  @include vp-767 {
    width: 100%;
    padding: 20px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 15px;
  z-index: 1;

  width: 40px;
  height: 40px;
  padding: 0;

  background: $color-transparent;
  color: $color-default-white;
  border: none;
  cursor: pointer;
}

.modal--responsive {
  .modal__content {
    width: 100%;
  }
}

.modal--fit-content {
  .modal__content {
    width: auto;

    @include vp-767 {
      width: 100%;
    }
  }
}

.modal--no-scale {
  transform: none;
}

.modal--preload {
  transition: none;
}

.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal form {
  display: flex;
  flex-direction: column;
}

.modal h2 {
  font-size: 30px;
  line-height: 140.62%;
  margin-left: 5px;
  margin-bottom: 4px;
}

.modal p {
  margin-bottom: 39px;
  margin-left: 5px;
}

.modal textarea {
  margin-bottom: 6px;
}

.modal button {
  margin-right: 0;
}

